import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { BrowserModule }  from '@angular/platform-browser';
import { WidgetModule } from './Widget';
import { AppComponent } from './App/component/App';
import { IndexComponent } from './App/component/Index';
import { HeaderComponent } from './App/component/header/Header';
import { ObjectWatcherComponent } from './App/component/objectWatcher/Index';
import { ObjectWatcherObjectNodeComponent } from './App/component/objectWatcher/objectNode/ObjectNode';
import { XmlMakerForFuComponent } from './App/component/xmlMakerForFu/Index';
import { XmlMakerForFuModuleXmlComponent } from './App/component/xmlMakerForFu/moduleXml/ModuleXml';
import { XmlMakerForFuTypeXmlComponent } from './App/component/xmlMakerForFu/typeXml/TypeXml';
import { XmlMakerForFuCommandXmlComponent } from './App/component/xmlMakerForFu/commandXml/CommandXml';
import { JsonSchema2PojoComponent } from './App/component/jsonschema2pojo/Index';
// service
import { ModuleXmlService } from './App/component/xmlMakerForFu/moduleXml/Service';
import { TypeXmlService } from './App/component/xmlMakerForFu/typeXml/Service';
import { XmlForFuService } from './App/component/xmlMakerForFu/Service';
import { CommandXmlService } from './App/component/xmlMakerForFu/commandXml/Service';
import { JsonSchema2PojoService } from './App/component/jsonschema2pojo/Service';
@NgModule({
    imports: [
        WidgetModule,
        BrowserModule,
        FormsModule,
        RouterModule.forRoot([
            {
                path: "",
                component: IndexComponent,
                children: [
                    {
                        path: "",
                        component: ObjectWatcherComponent
                    },
                    {
                        path: "objectWatcher",
                        component: ObjectWatcherComponent
                    },
                    {
                        path: "xmlMakerForFu",
                        component: XmlMakerForFuComponent
                    },
                    {
                        path: "jsonSchema2Pojo",
                        component: JsonSchema2PojoComponent
                    }
                ]
            }
        ])
    ],
    declarations: [
        AppComponent,
        IndexComponent,
        HeaderComponent,
        ObjectWatcherComponent,
        ObjectWatcherObjectNodeComponent,
        XmlMakerForFuComponent,
        XmlMakerForFuModuleXmlComponent,
        XmlMakerForFuTypeXmlComponent,
        XmlMakerForFuCommandXmlComponent,
        JsonSchema2PojoComponent
    ],
    providers: [
        ModuleXmlService,
        TypeXmlService,
        CommandXmlService,
        JsonSchema2PojoService,
        XmlForFuService
    ],
    bootstrap: [ AppComponent ]
})
export class AppModule { }



