/// <reference path="./../../../../util/jquery.d.ts" />
import { Component, OnInit, ElementRef } from '@angular/core';
import { JsonSchema2PojoService } from './Service';
import { ServerUrl } from "../../../../config/Common";
@Component({
    selector: 'vwvo-jsonschema2pojo',
    template: require('./index.html'),
    styles: [require('./index.css').toString()]
})
export class JsonSchema2PojoComponent implements OnInit{
    private ele: any;
    private editor: any;
    private isEditting: boolean = true;
    private generateFileList: any[];
    private isBusy: boolean = false;
    private isLoading: boolean = false;
    private loadingWidgetText: string = "doing...";
    private template: string = `    
    {    
      "type":"object",
      "properties": { 
        "foo": {   
          "type": "string" 
        },
        "bar": {
          "type": "integer"
        },
        "baz": {
          "type": "boolean"
        }  
      }
    }`;
    constructor(private jsonSchema2PojoService: JsonSchema2PojoService, private el: ElementRef) {
        this.ele = jQuery(el.nativeElement);
    }

    ngOnInit() {
        this.editor = CodeMirror.fromTextArea(this.ele.find("textarea").get(0), {
            lineNumbers: true,
            mode: "text/javascript",
            matchBrackets: true
        });
        this.editor.setValue(this.template);
        setTimeout(()=> {
            this.editor.refresh();
            this.editor.triggerOnKeyDown({});
        }, 10);
    }

    generateFile() {
        if(this.isBusy) {
            alert("当前正在同步数据，请稍后再操作");
            return;
        }

        let jsonStr: string = this.editor.getValue();
        let json: any;
        try {
            json = JSON.parse(jsonStr);
        } catch(e) {
            alert("数据格式不正确，请保证为完整的json格式");
            throw new Error(e);
        }

        this.isBusy = true;
        this.isLoading = true;
        this.jsonSchema2PojoService.generateFile({
            data: {
                json: jsonStr
            },
            successCallback: (e: any)=> {
                this.isBusy = false;
                this.isLoading = false;
                if(e.result) {
                    this.isEditting = false;
                    e.generateFileList.forEach((obj: any)=> {
                        obj.path = ServerUrl+obj.path;
                    });
                    this.generateFileList = e.generateFileList;
                } else {
                    alert("出错:"+e.msg);
                }
            },
            errorCallback: ()=> {
            }
        })
    }

    back() {
        this.isEditting = true;
    }
}



