/// <reference path="../../../../../util/jquery.d.ts" />
import { Component, Input, OnInit, ElementRef } from '@angular/core';
import { TypeXmlService } from './Service';
import { ServerUrl } from '../../../../../config/Common';
import { calcEnumArray2CodeEnum, calcStructArray2CodeStruct, fix2Array } from '../util/Index';
@Component({
    selector: 'vwvo-xmlMakerForFu-typeXml',
    template: require('./typeXml.html')
})
export class XmlMakerForFuTypeXmlComponent implements OnInit {
    @Input() active: boolean = false;
    private editting: boolean = false;
    private xml: string = ``;
    private code: string = ``;
    private name: string = '';
    private emptyJson: any = {
        root: {
            types: {
                enum: {
                    _: {
                        name: "DemoEnum"
                    },
                    item: {
                        _: {
                            name: "demo",
                            value: "1"
                        }
                    }
                }
            }
        }
    };
    private typeXmlList: any = [];
    private activeTypeXml: any;
    private isBusy: boolean = true;
    private ele: any;
    private editor: any;
    constructor(private typeXmlService: TypeXmlService, el: ElementRef) {
        this.ele = jQuery(el.nativeElement);
    }

    ngOnInit() {
        this.typeXmlService.list({
            successCallback: (list: any)=> {
                this.typeXmlList = list;
                this.isBusy = false;
            }
        });
        this.editor = CodeMirror.fromTextArea(this.ele.find("textarea").get(0), {
            lineNumbers: true,
            mode: "text/xml",
            matchBrackets: true
        });
    }

    removeTypeXmlById(id: string) {
        let typeXmlList = this.typeXmlList;
        for(let i=0, len=typeXmlList.length; i<len; i++) {
            if(typeXmlList[i].id == id) {
                typeXmlList.splice(i, 1);
                return;
            }
        }
    }

    checkExistTypeIdOrName(id: string, mName: string) {
        let typeXmlList = this.typeXmlList;
        for(let i=0, len=typeXmlList.length; i<len; i++) {
            let typeXml = typeXmlList[i];
            if(typeXml.id !== id && typeXml.name == mName) {
                return true;
            }
        }
    }

    changeTypeXml(typeXml: any) {
        if(this.isBusy) {
            alert("当前存在同步操作，请稍后");
            return;
        }
        if(this.editting) {
            alert("请先保存当前编辑的部分");
            return;
        }

        this.typeXmlList.forEach((obj: any)=> {
            obj.active = obj.id == typeXml.id;
        }); 
        this.activeTypeXml = typeXml;
        this.xml = $.json2Xml(this.activeTypeXml.json);
        this.code = this.calcJSON2Code(this.xml);
        this.name = this.activeTypeXml.name;
        this.editor.setValue("");
        setTimeout(()=> {
            this.editor.refresh();
            this.editor.triggerOnKeyDown({});
        }, 10);
    }

    editTypeXml() {
        if(this.isBusy) {
            alert("当前存在同步操作，请稍后");
            return;
        }
        if(!this.activeTypeXml) {
            return;
        }

        if(this.editting) {
            alert("请先保存当前编辑的部分");
            return;
        }

        this.typeXmlService.checkVersion({
            data: {
                id: this.activeTypeXml.id,
                version: this.activeTypeXml.version
            },
            successCallback: (e: any)=> {
                this.editting = true;
                this.xml = $.json2Xml(this.activeTypeXml.json);
                this.name = this.activeTypeXml.name;
                this.editor.setValue(this.xml);
                setTimeout(()=> {
                    this.editor.refresh();
                    this.editor.triggerOnKeyDown({});
                }, 10);
            },
            failCallback: (e: any)=> {
                alert(e.msg);
                return;
            }
        });
    }

    createTypeXml() {
        if(this.isBusy) {
            alert("当前存在同步操作，请稍后");
            return;
        }

        if(this.editting) {
            alert("请先保存当前编辑的部分");
            return;
        }

        this.isBusy = true;
        let tmpTimespan = +new Date();
        this.typeXmlService.create({
            data: {
                name: +new Date(),
                jsonStr: JSON.stringify(this.emptyJson)
            },
            successCallback: (e: any)=> {
                this.isBusy = false;
                e.typeXml.json = JSON.parse(e.typeXml.jsonStr);
                this.typeXmlList.push(e.typeXml);
                this.changeTypeXml(e.typeXml);
            }
        });
    }

    removeTypeXml() {
        if(this.isBusy) {
            alert("当前存在同步操作，请稍后");
            return;
        }
        if(!this.activeTypeXml) {
            return;
        }
        if(!confirm("确定要删除该文件么?")) {
            return;
        }

        this.typeXmlService.remove({
            data: {
                id: this.activeTypeXml.id,
                name: this.activeTypeXml.name
            },
            successCallback: ()=> {
                this.removeTypeXmlById(this.activeTypeXml.id);
                this.isBusy = false;
                this.activeTypeXml = null;
                this.code = this.xml = this.name = null;
                this.editting = false;
            }
        });
    }

    cancelEditXml() {
        try {
            let xml = this.editor.getValue().trim();
            let jsonStr: string = JSON.stringify($.xml2json(xml));
            if(jsonStr !== this.activeTypeXml.jsonStr && !confirm("您已经修改过该文件，确定取消编辑操作?")) {
                return;
            }

            this.isBusy = false;
            this.editting = false;
        } catch(e) {
            alert("请检查格式");
        }
    }

    saveTypeXml() {
        if(this.isBusy) {
            alert("当前存在同步操作，请稍后");
            return;
        }

        if(!this.activeTypeXml) {
            return;
        }

        if(this.checkExistTypeIdOrName(this.activeTypeXml.id, this.activeTypeXml.name)) {
            alert("存在相同模块名称或者id的模块");
            return;
        }
        try {
            let xml = this.editor.getValue().trim();
            this.isBusy = true;
            this.code = this.calcJSON2Code(xml);
            this.activeTypeXml.json = $.xml2json(xml);
            this.activeTypeXml.jsonStr = JSON.stringify(this.activeTypeXml.json);
            this.editting = false;

            this.typeXmlService.update({
                data: {
                    id: this.activeTypeXml.id,
                    jsonStr: this.activeTypeXml.jsonStr,
                    name: this.name,
                    xml: xml,
                    version: this.activeTypeXml.version
                },
                successCallback: (e: any)=> {
                    this.isBusy = false;
                    this.activeTypeXml.name = this.name;
                    this.activeTypeXml.version = e.version;
                    this.activeTypeXml.downloadUrl = ServerUrl+"xmlForFu/"+this.name+".xml";
                },
                failCallback: (e: any)=> {
                    this.isBusy = false;
                    alert(e.msg);
                }
            });
        } catch(e) {
            alert("操作发生错误，请检查xml是否正确或完整，或刷新页面即可操作");
            this.isBusy = false;
        }
    }

    calcJSON2Code(xml: string) {
        let json: any;
        try {
            json = $.xml2json(xml);
        } catch(e) {
            alert(e.message);
            console.log(e);
            throw new Error(e);
        }

        json = json.root;
        let types: any = json.types;
        let struts: any[] = fix2Array(types.struct);
        let enums: any[] = fix2Array(types.enum);
        let html = ``;
        html += calcStructArray2CodeStruct(struts);
        html += calcEnumArray2CodeEnum(enums);

        return html;
    }
}


